footer {
  background-color: var(--color-secondary);
  height: 120px;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.footer__logo > p,
.footer__contents {
  color: var(--color-main);
  font-weight: bold;
}

.footer__logo > p {
  margin-top: 0.5rem;
  display: flex;
  justify-content: center;
  font-size: 13px;
  width: 100%;
}

.footer__contents > p {
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer__socials {
  margin-top: 0.5rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.footer__icons {
  transition: color 0.3s ease;
}

.footer__icons:hover {
  color: black;
  cursor: pointer;
}

.footer__links {
  color: var(--color-main);
}


@media screen and (max-width: 500px) {

  footer {
    height: auto;
    flex-direction: column-reverse;
    padding: 3rem 0;
    justify-content: center;
    gap: 3rem;
  }
  
}